<template>
    <div id="app">
        <div class="window_con" @click="bodyClick" v-if="subjectId == 1">
            <div class="title">
                <div>结果统计<span v-if="corrcetProgress!=100">批改中...{{corrcetProgress}}%</span></div>
                <el-radio-group v-model="currMode" @change="changeMode" fill="#8BADCE" text-color="#FFFFFF">
                    <el-radio-button label="答题统计"></el-radio-button>
                    <el-radio-button label="学生统计"></el-radio-button>
                </el-radio-group>
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
            <div class="main_con">
                <div class="main_wrap" v-loading="quesLoading">
                    <div class="table_con" ref="tableCon" v-show="currMode!=='学生统计'">
                        <div class="tabHeader">
                            <div class="tit">题号</div>
                            <div class="tit el-table"
                                :class="{'asc':order[currLevel]=='asc', 'desc':order[currLevel]=='desc'}"
                                @click="orderBy">
                                正确率
                                <span class="caret-wrapper"><i class="sort-caret ascending"></i><i
                                        class="sort-caret descending"></i></span>
                            </div>
                            <div class="tip">
                                <div class="rect green"></div>正确
                                <div class="rect red"></div>错误
                            </div>
                        </div>
                        <vue-scroll>
                            <div class="treeWrap">
                                <vue-scroll>
                                    <el-tree ref="treeProgress" :props="treeProps" :data="tableData" node-key="qaId"
                                        :accordion="true" :highlight-current="true" :expand-on-click-node="true"
                                        @node-expand="treeExpand" @node-collapse="treeCollapse"
                                        @node-click="setCurrent">
                                        <span class="custom-tree-node" slot-scope="{ node, data }">
                                            <div v-if="node.level==1">
                                                <div class="label">{{node.label}}</div>
                                                <el-progress :text-inside="true" :stroke-width="23*dpr" color="#86E062"
                                                    :percentage="parseInt(data.rightStuList.length*100/data.stuTotal)?parseInt(data.rightStuList.length*100/data.stuTotal):0">
                                                </el-progress>
                                            </div>
                                            <div class="leaf" v-else>
                                                <div class="label">{{node.label}}</div>
                                                <el-progress :text-inside="true" :stroke-width="23*dpr" color="#86E062"
                                                    :percentage="parseInt(data.rightStuList.length*100/data.stuTotal)?parseInt(data.rightStuList.length*100/data.stuTotal):0">
                                                </el-progress>
                                            </div>
                                        </span>
                                    </el-tree>
                                </vue-scroll>
                            </div>
                        </vue-scroll>
                    </div>
                    <div class="ques_details" ref="ques_details" v-show="currMode=='答题统计'" v-loading="detailLoading">
                        <vue-scroll>
                            <div class="chart_tab">
                                <div class="quesInfo" v-if="currQuesInfo">
                                    <div class="quesNum">{{currQuesInfo.questionName}}</div>
                                    <div class="quesImg">
                                        <div ref="questionImg">
                                            <template v-for="(item,index) in currQuesInfo.cutImage" v-if="!currSelStu">
                                                <el-image :src="item" class="questionImg" @load="quesImgLoaded"
                                                    fit="contain">
                                                </el-image>
                                            </template>
                                            <template v-else>
                                                <!-- <el-image :src="currSelStu.correctImg" class="questionImg"
                                                     object-fit="contain"></el-image> -->
                                                <el-image
                                                    :src="currSelStu.correctImg?currSelStu.correctImg.replace(/-Mark/, '') : currQuesInfo.cutImage[0]"
                                                    class="questionImg" object-fit="contain"></el-image>
                                            </template>
                                            <template v-for="(item,idx) in areaBorderStyle" v-if="currSelStu">
                                                <el-popover v-if="currQuesDetail" placement="bottom-start"
                                                    v-model="popVisible[idx]" popper-class="editPopper"
                                                    :visible-arrow="false" trigger="manual">
                                                    <el-button-group class="editBtns">
                                                        <el-button type="primary" @click="updateBaseRes(1,idx)">
                                                            <img src="@/assets/icon_correct.png" />
                                                            <!-- <div class="type right">正确</div> -->
                                                        </el-button>
                                                        <el-button type="primary" @click="updateBaseRes(0,idx)">
                                                            <img src="@/assets/icon_error.png" />
                                                            <!-- <div class="type wrong">错误</div> -->
                                                        </el-button>
                                                    </el-button-group>
                                                    <i class="areaBorder" slot="reference" :style="item"
                                                        @click.stop="openPop(idx)"></i>
                                                </el-popover>
                                            </template>

                                            <template v-for="(item,index) in currSelStuRes">
                                                <img class="resIcon" src="@/assets/tick.png" v-if="item.corrResult == 1"
                                                    v-bind:style="{left:item.x+'px',top:item.y+'px',height: item.h+'px'}" />
                                                <img class="resIcon" src="@/assets/cross.png"
                                                    v-if="item.corrResult == 0"
                                                    v-bind:style="{left:item.x+'px',top:item.y+'px',height: item.h+'px'}" />
                                                <img class="resIcon" src="@/assets/icon_doubt.png"
                                                    v-if="item.corrResult == 4"
                                                    v-bind:style="{left:item.x+'px',top:item.y+'px',height: item.h+'px'}" />
                                            </template>
                                        </div>

                                    </div>
                                    <div class="answer">
                                        <div class="answer_tit" v-if="currQuesDetail">参考答案：</div>
                                        <div class="answer_con" v-if="currQuesDetail" ref="mathJaxEl"
                                            v-html="currQuesDetail.answer.split(',')[currKongIndex]">
                                        </div>
                                        <div class="btn_analysis" @click="taggleAnalysis" v-if="currQuesDetail">
                                            {{showAnalysis?"隐藏解析":"查看解析"}}</div>
                                    </div>
                                    <div class="answer analysis" :class="{open: showAnalysis}" v-if="currQuesDetail">
                                        <div class="answer_tit">题目解析：</div>
                                        <div class="answer_con" ref="mathJaxEl"
                                            v-html="currQuesDetail.analysis?currQuesDetail.analysis:'此题暂无解析'">

                                        </div>
                                    </div>
                                </div>
                                <div class="kong_tab" v-if="currQuesDetail">
                                    <div class="tit">作答统计：</div>

                                    <div class="kongList"
                                        v-show="currQuesDetail.stuList[0].resultMap.itemInfos.length>1">
                                        <vue-scroll :ops="ops">
                                            <div class="kongItem" :class="{active: currKongIndex==item.questionIndex}"
                                                v-for="(item,index) in currQuesDetail.stuList[0].resultMap.itemInfos"
                                                @click="setKongIndex(item.questionIndex)">
                                                空{{index+1}}</div>
                                        </vue-scroll>
                                    </div>

                                </div>
                                <div class="students" v-if="currQuesDetail">
                                    <el-collapse accordion>
                                        <el-collapse-item>
                                            <template slot="title">
                                                <div class="stuNum right">
                                                    正确：{{rightGroup.length}}人</div>
                                            </template>
                                            <div class="stuList">
                                                <div class="stuItem"
                                                    :class="{active: currSelStu? item.deviceMac == currSelStu.deviceMac : false }"
                                                    v-for="(item,index) in rightGroup" v-if="rightGroup"
                                                    @click="selStu(item,index)">{{item.stuName}}</div>
                                            </div>
                                        </el-collapse-item>
                                        <el-collapse-item>
                                            <template slot="title">
                                                <div class="stuNum wrong">
                                                    错误：{{wrongGroup.length}}人</div>
                                            </template>
                                            <div class="stuList">
                                                <div class="stuItem"
                                                    :class="{active: currSelStu? item.deviceMac == currSelStu.deviceMac : false }"
                                                    v-for="(item,index) in wrongGroup" v-if="wrongGroup"
                                                    @click="selStu(item,index)">{{item.stuName}}</div>
                                            </div>
                                        </el-collapse-item>
                                        <el-collapse-item>
                                            <template slot="title">
                                                <div class="stuNum none">
                                                    未作答：{{undoGroup.length}}人</div>
                                            </template>
                                            <div class="stuList">
                                                <div class="stuItem"
                                                    :class="{active: currSelStu? item.deviceMac == currSelStu.deviceMac : false }"
                                                    v-for="(item,index) in undoGroup" @click="selStu(item,index)">
                                                    {{item.stuName}}</div>
                                            </div>
                                        </el-collapse-item>
                                        <el-collapse-item>
                                            <template slot="title">
                                                <div class="stuNum none">
                                                    无法批改：{{uncorrGroup.length}}人</div>
                                            </template>
                                            <div class="stuList">
                                                <div class="stuItem"
                                                    :class="{active: currSelStu? item.deviceMac == currSelStu.deviceMac : false }"
                                                    v-for="(item,index) in uncorrGroup" @click="selStu(item,index)">
                                                    {{item.stuName}}</div>
                                            </div>
                                        </el-collapse-item>
                                    </el-collapse>
                                </div>
                                <div class="charts" v-if="currChartData.length>0">
                                    <div class="chart_info" v-if="currChartData">
                                        <div class="chart_tit">错误答案前三</div>
                                        <div class="chart_con">
                                            <div class="mask"></div>
                                            <v-chart :options="option" ref="answerBar" :autoresize="true" @click="" />
                                        </div>
                                        <div class="chart_tip">
                                            <div class="tip">
                                                <div class="color gray"></div>
                                                <div class="num">{{currChartData[0].value}}人</div>
                                                <div class="ans">
                                                    <katex-element :expression="currChartData[0].name" />
                                                </div>
                                            </div>
                                            <div class="tip" v-if="currChartData.length>1">
                                                <div class="color blue"></div>
                                                <div class="num">{{currChartData[1].value}}人</div>
                                                <div class="ans">
                                                    <katex-element :expression="currChartData[1].name" />
                                                </div>
                                            </div>
                                            <div class="tip" v-if="currChartData.length>2">
                                                <div class="color purp"></div>
                                                <div class="num">{{currChartData[2].value}}人</div>
                                                <div class="ans">
                                                    <katex-element :expression="currChartData[2].name" />
                                                </div>
                                            </div>
                                        </div>
                                    </div>

                                </div>
                            </div>
                        </vue-scroll>
                    </div>
                    <div class="tableStat" v-show="currMode=='学生统计'" v-loading="tableLoading"
                        element-loading-text="批改数据同步中" element-loading-spinner="el-icon-loading"
                        element-loading-background="rgba(255, 255, 255, 0.8)">
                        <el-table v-if="stuData" :data="stuData" style="width: 100%" ref="stuTable"
                            @row-click="reportStu" :max-height="tableHeight">
                            <el-table-column prop="name" label="姓名" sortable
                                :sort-orders="['descending','ascending', null]" align="center"></el-table-column>
                            <el-table-column prop="stuNum" label="学号" sortable
                                :sort-orders="['descending','ascending', null]" align="center"></el-table-column>
                            <el-table-column prop="ratio" label="正确率" sortable
                                :sort-orders="['descending','ascending', null]" align="center">
                                <template slot-scope="scope">
                                    {{scope.row.ratio + '%'}}
                                </template>
                            </el-table-column>
                            <el-table-column prop="totalTime" label="用时" sortable
                                :sort-orders="['descending','ascending', null]" align="center"></el-table-column>
                        </el-table>
                    </div>
                </div>

            </div>


        </div>
        <div class="noSta" v-else>
            <div class="tipCon">
                <img src="@/assets/tip_dev.png">
                <div class="tip">{{subjectId==2?"英语":""}}{{subjectId==3?"语文":""}}批改功能即将上线，敬请期待！</div>
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div>
        </div>
    </div>
</template>
<script>
    import { formatDate } from '@/assets/date.js';
    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    import ECharts from 'vue-echarts'
    import 'echarts/lib/chart/pie'
    var timer = null;


    export default {
        name: 'App',
        components: {
            'v-chart': ECharts
        },
        data() {
            return {
                currMode: "答题统计",
                dpr: 1,
                treeProps: {
                    children: 'recordTree',
                    label: 'questionName'
                },
                ops: {
                    scrollPanel: {
                        scrollingX: true,
                        scrollingY: false
                    }
                },
                taskId: null,
                subjectId: 1,
                taskIds: null,
                tableData: null,
                paperImg: [],
                currentRow: null,
                isExpand: false,
                order: ["", "", ""],
                currLevel: 0,
                currKongIndex: 0,
                quesLoading: false,
                detailLoading: false,
                imgLoading: false,
                tableLoading: false,
                showAnalysis: false,
                tableHeight: 0,
                currQuesInfo: null,
                currQuesDetail: null,
                localQuesInfo: [],
                originQuesInfo: [],
                updatedList: [],
                imgScale: 0,
                currImgHeight: 0,
                currSelStu: null,
                currStuIndex: 0,
                popVisible: [false],
                currDetailIndex: 0,
                currKongInfo: null,
                kongList: null,
                stuData: null,
                imgInfo: null,
                mathContent: null,
                corrcetProgress: 100,
                option: {
                    color: ['#C2DCED', '#4CC3FF', '#8D83FE'],
                    series: [
                        {
                            name: '',
                            type: 'pie',
                            radius: ['60%', '90%'],
                            avoidLabelOverlap: false,
                            hoverAnimation: false,
                            legendHoverLink: false,
                            label: {
                                normal: {
                                    show: false,
                                    position: 'center'
                                },
                                emphasis: {
                                    show: false,
                                    textStyle: {
                                        fontSize: '30',
                                        fontWeight: 'bold'
                                    }
                                }
                            },
                            emphasis: {
                                itemStyle: {
                                    opacity: 1,
                                    color: "#C2DCED"
                                }
                            },
                            labelLine: {
                                normal: {
                                    show: false
                                }
                            },
                            data: []
                        }
                    ]
                }
            }
        },

        computed: {
            areaBorderStyle: function () {
                let arr = []
                let currImg = this.currQuesInfo
                if (this.currQuesDetail && this.currSelStu && this.currSelStu.correctImg) {
                    let kong = this.currQuesDetail.stuList[0].resultMap.itemInfos[this.currKongIndex]
                    console.log(this.currQuesDetail.questionType)
                    console.log(this.currSelStu)
                    if (this.currQuesDetail.questionType == 15) {
                        let items = this.currSelStu.resultMap.inLineItems
                        console.log(items)
                        for (let i = 0; i < items.length; i++) {
                            const item = items[i];
                            let obj = {}
                            obj.left = parseInt((item.writeLocation[0] - currImg.location[0][0]) * this.imgScale + 20 * this.dpr) + 'px'
                            obj.top = parseInt((item.writeLocation[1] - currImg.location[0][1]) * this.imgScale + 20 * this.dpr) + 'px'
                            obj.width = parseInt(item.writeLocation[2] * this.imgScale) + 'px'
                            obj.height = parseInt(item.writeLocation[3] * this.imgScale) + 'px'
                            arr.push(obj)
                        }
                    } else {
                        let obj = {}
                        console.log(kong)
                        console.log(currImg)
                        console.log(kong.itemLocation[0])
                        console.log(currImg.location[0])
                        console.log(this.imgScale)
                        obj.left = parseInt((kong.itemLocation[0] - currImg.location[0][0]) * this.imgScale + 20 * this.dpr) + 'px'
                        obj.top = parseInt((kong.itemLocation[1] - currImg.location[0][1]) * this.imgScale + 20 * this.dpr) + 'px'
                        obj.width = parseInt(kong.itemLocation[2] * this.imgScale) + 'px'
                        obj.height = parseInt(kong.itemLocation[3] * this.imgScale) + 'px'
                        arr.push(obj)
                    }
                } else {

                }
                console.log(arr)
                return arr
            },
            rightGroup: function () {
                let i = this.currKongIndex
                if (this.currQuesDetail) {
                    let group;
                    if (this.currQuesDetail.questionType != 15) {
                        group = this._.filter(this.currQuesDetail.stuList, function (o) { return o.resultMap.inLineItems[i].corrResult == 1 })
                    } else {
                        group = this._.filter(this.currQuesDetail.stuList, function (o) { return o.rightSpaceTotal == o.spaceTotal && o.resultMap.inLineItems[i].corrResult == 1 })
                    }
                    return group
                } else {
                    return []
                }

            },
            wrongGroup: function () {
                let i = this.currKongIndex
                if (this.currQuesDetail) {
                    let group;
                    if (this.currQuesDetail.questionType != 15) {
                        group = this._.filter(this.currQuesDetail.stuList, function (o) { return o.resultMap.inLineItems[i].corrResult == 0 })
                    } else {
                        group = this._.filter(this.currQuesDetail.stuList, function (o) { return o.rightSpaceTotal != o.spaceTotal })
                    }
                    return group
                } else {
                    return []
                }
            },
            halfGroup: function () {
                let i = this.currKongIndex
                if (this.currQuesDetail) {
                    let group = this._.filter(this.currQuesDetail.stuList, function (o) {
                        return o.resultMap.inLineItems[i].corrResult == 2
                    })
                    return group
                } else {
                    return []
                }
            },
            undoGroup: function () {
                let i = this.currKongIndex
                if (this.currQuesDetail) {
                    let group = this._.filter(this.currQuesDetail.stuList, function (o) {
                        return o.resultMap.inLineItems[i].corrResult == 3
                    })
                    return group
                } else {
                    return []
                }
            },
            uncorrGroup: function () {
                let i = this.currKongIndex
                if (this.currQuesDetail) {
                    let group = this._.filter(this.currQuesDetail.stuList, function (o) {
                        return o.resultMap.inLineItems[i].corrResult == 4
                    })
                    return group
                } else {
                    return []
                }
            },
            currSelStuRes: function () {
                console.log("更新了")
                let arr = []
                if (this.currSelStu && this.currSelStu.correctImg) {
                    arr = this.currSelStu.resultMap.inLineItems
                } else {
                    arr = null
                }
                console.log(arr)
                if (arr) {
                    let computedArr = []
                    for (const item of arr) {
                        let location = item.writeLocation
                        let w = location[2] * this.imgScale
                        let h = location[3] > 100 ? 100 * this.dpr * this.imgScale : location[3] * this.imgScale
                        let x = (location[0] - this.currQuesInfo.location[0][0]) * this.imgScale + 20 * this.dpr + w
                        let y = (location[1] - this.currQuesInfo.location[0][1]) * this.imgScale + 20 * this.dpr

                        let obj = {
                            x: x,
                            y: y,
                            w: w,
                            h: h,
                            spaceId: item.spaceId,
                            corrResult: item.corrResult,
                        }
                        computedArr.push(obj)
                    }
                    // console.log(computedArr)
                    return computedArr
                } else {
                    return arr
                }
            },
            currChartData: function () {
                let pieData = []
                if (this.wrongGroup.length > 0) {
                    // console.log(this.wrongGroup)
                    let arrName = []
                    let names = []
                    for (let i = 0; i < this.wrongGroup.length; i++) {
                        let name = this.wrongGroup[i].resultMap.inLineItems[this.currKongIndex].recognizeResult
                        names.push(name)
                    }
                    // console.log(names)
                    var countedValues = names.reduce(function (allNames, name) {
                        if (name in allNames) {
                            allNames[name]++;
                        }
                        else {
                            allNames[name] = 1;
                            arrName.push(name)
                        }
                        return allNames;
                    }, {});
                    // console.log(countedValues)
                    for (let i = 0; i < arrName.length; i++) {
                        let name = arrName[i];
                        let value = countedValues[name]
                        // if (this.currQuesInfo.questionType == "口算题") {
                        //     console.log(name)
                        //     let reg = /\((.+?)\)/g;
                        //     let res = name.match(reg)
                        //     console.log(res)
                        //     if (res.length) {
                        //         name = res[0].substring(1, res[0].length - 1)
                        //         console.log(name)
                        //     }
                        // }
                        pieData.push({ 'name': name, 'value': value })
                    }
                    var sortData = function (a, b) {
                        return b.value - a.value
                    }
                    pieData.sort(sortData);
                    this.option.series[0].data = pieData
                }
                // console.log(pieData)
                return pieData
            }
        },
        filters: {
            formatDate(time) {
                let date = new Date(time);
                return formatDate(date, 'hh:mm');
            },
            decodePaperName(str) {
                return Base64.decode(str.split(",")[0].split(":")[1]);
            }
        },
        beforeDestroy() {
            // this.updateResList()
        },
        mounted() {
            //获取url参数
            let that = this

            window.correctCallback = this.correctCallback
            let taskId = utils.getUrlPrams("taskId")
            let subjectId = utils.getUrlPrams("subjectId")
            this.taskId = taskId
            this.subjectId = subjectId
            this.dpr = localStorage.getItem("width") / 1920

            if (subjectId == 1) {
                this.getTaskData(taskId)
                this.getStuTable(taskId)
            }

        },
        updated() {
            this.renderMathJax()
        },
        methods: {
            getStuTable(taskId) {
                let that = this
                this.tableLoading = true
                axios.get(API.api_tl + 'api/selInteractRecord', {
                    params: {
                        taskId: taskId
                    }
                }).then(function (res) {
                    console.log(res.data.data.interactStuList)
                    that.tableLoading = false
                    that.stuData = res.data.data.interactStuList
                }).catch(function (error) {
                    console.log(error);
                });
            },
            correctCallback(res) {
                if (res == 1) {
                    this.updatedList = []
                    this.getStuTable(this.taskId)
                }
            },
            setKongIndex(index) {
                this.currKongIndex = index
            },
            bodyClick() {
                for (let i = 0; i < this.popVisible.length; i++) {
                    if (this.popVisible[i]) {
                        this.$set(this.popVisible, i, false)
                    }
                }

            },
            openPop(index) {
                console.log(index)
                this.bodyClick()
                if (this.currSelStu) {
                    this.$set(this.popVisible, index, this.popVisible[index] = !this.popVisible[index])

                }
            },
            updateBaseRes(newValue, index) {
                this.bodyClick()
                console.log(newValue, index)
                let currStu = this.localQuesInfo[this.currDetailIndex].stuList[this.currStuIndex]
                console.log(currStu)
                let kong;
                if (this.currQuesDetail.questionType == 15) {
                    kong = currStu.resultMap.inLineItems[index]
                } else {
                    kong = currStu.resultMap.inLineItems[this.currKongIndex]
                }
                let oldValue = kong.corrResult
                if (oldValue !== newValue) {
                    this.$set(kong, "corrResult", newValue)
                    let img = this.currSelStu.correctImg.replace(/-Mark/, '').split("?")[0]
                    let parperIdx = this._.indexOf(this.paperImg, img)
                    let obj = {}
                    obj.spaceId = kong.spaceId
                    obj.deviceMac = currStu.deviceMac
                    obj.correctResult = newValue
                    obj.x = kong.writeLocation[0]
                    obj.y = kong.writeLocation[1]
                    obj.w = kong.writeLocation[2]
                    obj.h = kong.writeLocation[3]
                    if (parperIdx == -1) {
                        this.paperImg.push(img)
                        obj.pageIndex = this.paperImg.length - 1
                    } else {
                        obj.pageIndex = parperIdx
                    }
                    let kongIdx = this._.findIndex(this.updatedList, ["spaceId", obj.spaceId])
                    if (kongIdx == -1) {
                        this.updatedList.push(obj)
                    } else {
                        this.updatedList[kongIdx].correctResult = newValue
                    }
                    console.log(this.updatedList)
                    if (newValue == 1) {
                        currStu.rightSpaceTotal = currStu.rightSpaceTotal + 1
                        console.log(currStu.rightSpaceTotal)
                        console.log(currStu.spaceTotal)
                        if (currStu.rightSpaceTotal == currStu.spaceTotal) {
                            console.log("--------这个学生全对了----------")
                            this.updateTree(currStu.deviceMac, 1)
                        }
                    } else {
                        if (currStu.rightSpaceTotal == currStu.spaceTotal) {
                            console.log("--------这个学生不是全对了----------")
                            this.updateTree(currStu.deviceMac, 0)
                        }
                        currStu.rightSpaceTotal = currStu.rightSpaceTotal - 1
                    }
                    this.currQuesDetail = JSON.parse(JSON.stringify(this.localQuesInfo[this.currDetailIndex]))
                    console.log(this.currQuesDetail)
                }

            },
            updateTree(stu, status) {
                console.log(stu, status)
                let qaId = this.currQuesDetail.qaId
                for (let i = 0; i < this.tableData.length; i++) {
                    const l1 = this.tableData[i];
                    if (l1.qaId == qaId) {
                        let curr = this.tableData[i]
                        if (status) {
                            curr.rightStuList.push(stu)
                        } else {
                            let index = this._.indexOf(curr.rightStuList, stu)
                            this.$delete(curr.rightStuList, index)
                        }
                        curr.rate = parseInt(curr.rightStuList.length * 100 / curr.stuTotal)
                    } else if (l1.recordTree) {
                        for (let j = 0; j < l1.recordTree.length; j++) {
                            const l2 = l1.recordTree[j];
                            if (l2.qaId == qaId) {
                                let curr = this.tableData[i].recordTree[j]
                                console.log(curr.rightStuList)
                                if (status) {
                                    curr.rightStuList.push(stu)
                                    let flag = true
                                    for (let m = 0; m < this.tableData[i].recordTree.length; m++) {
                                        const p1 = this.tableData[i].recordTree[m];
                                        if (p1.rightStuList.indexOf(stu) == -1) {
                                            flag = false
                                            return
                                        }
                                    }
                                    if (flag) {
                                        this.tableData[i].rightStuList.push(stu)
                                    }
                                } else {
                                    let index = this._.indexOf(curr.rightStuList, stu)
                                    this.$delete(curr.rightStuList, index)
                                    let p1index = this._.indexOf(this.tableData[i].rightStuList, stu)
                                    if (p1index != -1) {
                                        this.$delete(this.tableData[i].rightStuList, p1index)
                                    }
                                }
                                console.log(curr.rightStuList)
                                curr.rate = parseInt(curr.rightStuList.length * 100 / curr.stuTotal)
                                this.tableData[i].rate = parseInt(this.tableData[i].rightStuList.length * 100 / this.tableData[i].stuTotal)
                            } else if (l2.recordTree) {
                                for (let k = 0; k < l2.recordTree.length; k++) {
                                    const l3 = l2.recordTree[k];
                                    if (l3.qaId == qaId) {
                                        let curr = this.tableData[i].recordTree[j].recordTree[k]
                                        if (status) {
                                            curr.rightStuList.push(stu)
                                            let flag = true
                                            for (let m = 0; m < this.tableData[i].recordTree[j].recordTree.length; m++) {
                                                const p2 = this.tableData[i].recordTree[j].recordTree[m];
                                                if (p2.rightStuList.indexOf(stu) == -1) {
                                                    flag = false
                                                    return
                                                }
                                            }
                                            if (flag) {
                                                this.tableData[i].recordTree[j].rightStuList.push(stu)
                                                let flagp1 = true
                                                for (let m = 0; m < this.tableData[i].recordTree.length; m++) {
                                                    const p1 = this.tableData[i].recordTree[m];
                                                    if (p1.rightStuList.indexOf(stu) == -1) {
                                                        flagp1 = false
                                                        return
                                                    }
                                                }
                                                if (flagp1) {
                                                    this.tableData[i].rightStuList.push(stu)
                                                }
                                            }
                                        } else {
                                            let index = this._.indexOf(curr.rightStuList, stu)
                                            this.$delete(curr.rightStuList, index)
                                            let p2index = this._.indexOf(this.tableData[i].recordTree[j].rightStuList, stu)
                                            if (p2index != -1) {
                                                this.$delete(this.tableData[i].recordTree[j].rightStuList, p2index)
                                                let p1index = this._.indexOf(this.tableData[i].rightStuList, stu)
                                                if (p1index != -1) {
                                                    this.$delete(this.tableData[i].rightStuList, p1index)
                                                }
                                            }
                                        }
                                        curr.rate = parseInt(curr.rightStuList.length * 100 / curr.stuTotal)
                                        this.tableData[i].rate = parseInt(this.tableData[i].rightStuList.length * 100 / this.tableData[i].stuTotal)
                                        this.tableData[i].recordTree[j].rate = parseInt(this.tableData[i].recordTree[j].rightStuList.length * 100 / this.tableData[i].recordTree[j].stuTotal)
                                    }
                                }
                            }
                        }
                    }
                }
            },
            initImgHeight() {
                //解决切换图片后页面跳动问题
                let currImgHeight = 0
                if (this.imgScale && this.currQuesInfo) {
                    console.log(this.imgScale)
                    if (this.currQuesInfo.location.length > 1) {
                        currImgHeight = this.imgScale * (this.currQuesInfo.location[0][3] + this.currQuesInfo.location[1][3])
                    } else {
                        currImgHeight = this.imgScale * this.currQuesInfo.location[0][3]
                    }
                }
                this.currImgHeight = parseInt(currImgHeight)
            },
            quesImgLoaded: function (event) {
                console.log(event)
                if (event) {
                    this.imgInfo = event
                    let naturalWidth = this.imgInfo.path[0].naturalWidth
                    let offsetWidth = this.imgInfo.path[0].offsetWidth
                    let scale = offsetWidth / naturalWidth
                    this.imgScale = scale
                }

            },
            renderMathJax() {
                if (window.MathJax) {
                    MathJax.Hub.Queue([
                        "Typeset",
                        MathJax.Hub,
                        document.getElementsByClassName("mathJax")
                    ])
                    window.MathJax.Hub.Queue(["Typeset", window.MathJax.Hub, this.$refs.mathJaxEl]);
                }
            },
            taggleAnalysis() {
                this.showAnalysis = !this.showAnalysis
            },
            getTaskData(taskId) {
                var that = this
                axios.get(API.api_qb + 'statistics/getTaskDynamic', {
                    params: {
                        taskIds: taskId
                    }
                }).then(function (res) {
                    let percent = parseInt(res.data.data[0].correctDeviceNumbers * 100 / res.data.data[0].deviceNumbers);
                    if (res.data.data[0].correctDeviceNumbers !== res.data.data[0].deviceNumbers && res.data.data[0].deviceNumbers != 0 && percent < 101) {
                        if (that.corrcetProgress !== percent) {
                            that.corrcetProgress = percent
                            that.getRate(taskId)
                        }
                        setTimeout(() => {
                            that.getTaskData(taskId)
                        }, 1000);
                    } else {
                        that.corrcetProgress = 100
                        that.getRate(taskId)
                        clearTimeout(timer)
                    }

                }).catch(function (error) {
                    console.log(error);
                });
            },
            selStu(stu, index) {
                console.log(stu)
                // this.imgLoading = true
                if (stu.correctImg) {
                    let newImg = new Image()
                    newImg.src = stu.correctImg
                    newImg.onload = () => {
                        // 图片加载成功后把地址给原来的img
                        this.currSelStu = stu
                        this.currStuIndex = this._.findIndex(this.currQuesDetail.stuList, ["deviceMac", stu.deviceMac])
                        // this.imgLoading = false
                    }
                } else {
                    this.currSelStu = stu
                }


            },
            closeWindow() {
                this.updateResList()
                window.cefQuery({
                    request: "closeWindowStatistics",   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            reportStu(row) {
                console.log(row)
                let deviceMac = row.deviceMac
                window.cefQuery({
                    request: "reportStu:" + deviceMac,   // 代表请求方法 example事件名称 data 为传入参数
                    onSuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            },
            updateResList() {
                let resList = this.updatedList
                let localStr = []
                for (let i = 0; i < this.localQuesInfo.length; i++) {
                    const element = this.localQuesInfo[i];
                    localStr.push(JSON.stringify(element))
                }
                let isChange = JSON.stringify(this.originQuesInfo) != JSON.stringify(localStr)
                console.log(JSON.stringify(this.originQuesInfo) != JSON.stringify(localStr))
                if (resList.length && isChange) {
                    this.tableLoading = true
                    let UP = {
                        "taskId": this.taskId,
                        "paperImg": this.paperImg,
                        "areaList": resList
                    }
                    console.log(UP)
                    window.cefQuery({
                        request: "updateResList:" + encodeURIComponent(JSON.stringify(UP)),   // 代表请求方法 example事件名称 data 为传入参数
                        onSuccess: function (response) {
                            console.log("invoke success response = " + response);
                        },
                        onFailure: function (ecode, emsg) {
                            console.log("invoke fail code = " + ecode + " msg:" + emsg);
                        }
                    });
                }

            },
            orderBy() {
                console.log("isExpand----" + this.isExpand)
                console.log("currLevel----" + this.currLevel)
                console.log("orderBy----" + this.order[this.currLevel])
                if (this.order[this.currLevel] == "") {
                    this.$set(this.order, this.currLevel, "desc")
                } else if (this.order[this.currLevel] == "desc") {
                    this.$set(this.order, this.currLevel, "asc")
                } else if (this.order[this.currLevel] == "asc") {
                    this.$set(this.order, this.currLevel, "")
                }
                console.log(this.order)
                let ques = this.tableData
                let orderLevel1;
                let ruler = this.order[this.currLevel]
                if (this.currLevel == 0) {
                    if (ruler) {
                        orderLevel1 = this._.orderBy(ques, ['rate'], [ruler]);
                    } else {
                        orderLevel1 = this._.orderBy(ques, ['qaId'], ["asc"]);
                    }
                    this.tableData = orderLevel1
                } else if (this.currLevel == 1) {
                    for (let i = 0; i < ques.length; i++) {
                        let orderLevel2;
                        let qaId = ques[i].qaId
                        if (ruler) {
                            orderLevel2 = this._.orderBy(ques[i].recordTree, ['rate'], [ruler]);
                        } else {
                            orderLevel2 = this._.orderBy(ques[i].recordTree, ['qaId'], ["asc"]);
                        }
                        this.$refs.treeProgress.updateKeyChildren(qaId, orderLevel2)
                        // console.log(this.tableData[i])
                    }
                } else if (this.currLevel == 2) {
                    for (let i = 0; i < ques.length; i++) {
                        for (let j = 0; j < ques[i].recordTree.length; j++) {
                            if (ques[i].recordTree) {
                                let orderLevel3;
                                let qaId = ques[i].recordTree[j].qaId
                                if (ruler) {
                                    orderLevel3 = this._.orderBy(ques[i].recordTree[j].recordTree, ['rate'], [ruler]);
                                } else {
                                    orderLevel3 = this._.orderBy(ques[i].recordTree[j].recordTree, ['qaId'], ["asc"]);
                                }
                                this.$refs.treeProgress.updateKeyChildren(qaId, orderLevel3)
                            }

                            // console.log(this.tableData[i])
                        }

                    }
                }
            },
            treeExpand(obj, node) {
                console.log("展开了")
                console.log(obj)
                this.currLevel = obj.level
                console.log(node)
                this.isExpand = true
            },
            treeCollapse(obj, node) {
                console.log("关闭了")
                this.currLevel = obj.level - 1
                this.isExpand = false
            },
            setCurrent(obj, node, com) {
                this.bodyClick()
                console.log("点击了")

                this.currSelStu = null
                this.currQuesDetail = null
                console.log(this.currLevel)
                console.log(obj)
                let qaId = obj.qaId
                let questionId = obj.questionId
                this.currQuesInfo = obj
                this.initImgHeight()
                console.log(!obj.recordTree.length)
                if (!obj.recordTree.length) {
                    console.log(node)
                    console.log(com)
                    this.getQuesInfo(qaId, questionId)
                }
            },
            changeMode(e) {
                this.currMode = e
                this.updateResList()
            },
            getRate(id) {
                var that = this
                this.quesLoading = true
                axios.get(API.api_qb + 'tal/selTreeDirect', {
                    params: {
                        taskId: id
                    }
                }).then(function (res) {
                    console.log(res.data.data.areaInfo)
                    that.quesLoading = false
                    if (res.data.data.areaInfo) {
                        let first = res.data.data.areaInfo[0]
                        that.tableData = res.data.data.areaInfo
                        that.$nextTick(() => {
                            if (that.currQuesInfo) {
                                that.$refs.treeProgress.setCurrentKey(that.currQuesInfo.qaId)
                                that.setCurrent(that.currQuesInfo)
                            } else {
                                that.$refs.treeProgress.setCurrentKey(first.qaId)
                                that.setCurrent(first)
                            }
                        })
                    }

                }).catch(function (error) {
                    console.log(error);
                });
            },
            getQuesInfo(qaId, questionId) {
                this.showAnalysis = false
                this.detailLoading = true
                var that = this
                let taskId = this.taskId
                that.currSelStu = null
                console.log("=================")
                console.log(this.localQuesInfo)
                let isLocal = this._.findIndex(that.localQuesInfo, ['qaId', qaId])
                console.log(isLocal)
                if (isLocal == -1) {
                    axios.get(API.api_qb + 'tal/selQuestionItemInfo', {
                        params: {
                            qaId: qaId,
                            questionId: questionId,
                            taskId: taskId
                        }
                    }).then(function (res) {
                        that.detailLoading = false
                        console.log(res.data.data)
                        let QuesDetail = res.data.data
                        if (QuesDetail) {
                            that.currQuesDetail = QuesDetail
                            if (that.corrcetProgress == 100) {
                                that.localQuesInfo.push(QuesDetail)
                                that.originQuesInfo.push(JSON.stringify(QuesDetail))
                                console.log(that.originQuesInfo)
                                that.currDetailIndex = that.localQuesInfo.length - 1
                            }
                            that.currKongIndex = 0
                        }

                    }).catch(function (error) {
                        console.log(error);
                    });
                } else {
                    that.detailLoading = false
                    that.currDetailIndex = isLocal
                    that.currQuesDetail = that.localQuesInfo[that.currDetailIndex]
                    that.currKongIndex = 0
                }

            },

        }
    }
</script>
