import Vue from 'vue'
import App from './App.vue'
import _ from 'lodash'
import vuescroll from 'vuescroll';
import VueKatex from 'vue-katex'
import 'katex/dist/katex.min.css';
import 'vuescroll/dist/vuescroll.css';
import { Loading, Dialog, Button, ButtonGroup, RadioGroup, RadioButton, Table, TableColumn, Progress, Collapse, CollapseItem, Image, Tree, Popover } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '@/assets/flexible.js'

Vue.use(VueKatex)
Vue.use(vuescroll);
Vue.use(Loading);
Vue.use(Dialog);
Vue.use(Tree);
Vue.use(Button);
Vue.use(ButtonGroup);
Vue.use(Popover);
Vue.use(RadioGroup);
Vue.use(RadioButton);
Vue.use(Table);
Vue.use(TableColumn);
Vue.use(Progress);
Vue.use(Image);
Vue.use(Collapse);
Vue.use(CollapseItem);
Vue.prototype.$vuescrollConfig = {
    sizeStrategy: 'number',
    detectResize: true,
    bar: {
        keepShow: true,
        background: "#DDE6EE"
    },
    scrollPanel: {
        scrollingX: false
      }
};
Vue.prototype._ = _
Vue.config.productionTip = false

new Vue({
    render: h => h(App),
}).$mount('#app')